
body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.7;
}

main {
  width: 38ch;
  border: 2px solid black;
}

h1 {
  font-size: 24px;
  text-align: center;
  border-bottom: 2px solid black;
  margin: 10px auto;
}

img {
  display: inline-block;
}

ol {
  margin-left: 30px;
  list-style:none;
}

li { 
  font-size: 16px
}

li::before {
  $size: 18px;
  position: absolute;
  content: '';
  height: $size;
  width: $size;
  border: 1px solid black;
  transform: translateX(-40px) translateY(4px)
}